@font-face {
    font-family: Tahoma, Arial, sans-serif;
}

/*MAIN*/
.main {
    background-color: palegreen;
}

/*AVA & IMG*/
.ava {
    height: 100px;
    padding-top: 20px;
    display: inline-block;
}

.ava img {
    width: 50px;
    height: 50px;
    border-radius: 25px;

}

/*CONTENT & propses*/
.content {
    margin: 10px;
    width: 220px;
    height: 70px;
    margin-left: 26px;
    background: #363636;
    position: relative;
    border-radius: 5px;
    display: inline-block;
    line-height: 15px;
}

.content:before {
    content: "";
    width: 0;
    height: 0;
    position: absolute;
    right: 95%;
    top: calc(125% - 30px);
    border-top: 12px solid transparent;
    border-right: 25px solid #363636;
    border-bottom: 12px solid transparent;
    border-bottom: 10px;
}

.name {
    font-family: Tahoma, Arial, sans-serif;
    color: #BA3689;
    font-size: 15px;
    font-weight: bold;
    /*background-color: coral;*/
    padding: 10px 10px;
}

.message {

    color: white;
    padding-left: 10px;
    /*background-color: blueviolet;*/
}


.time {
    font-size: 14px;
    color: #595758;
    padding: 7px;

}


