.superInput {
    width: 300px;
    font-size: 13px;
    padding: 6px 0 4px 10px;
    border: 1px solid #cecece;
    background: #F6F6f6;
    border-radius: 8px;
}

.errorInput {
    width: 300px;
    font-size: 13px;
    padding: 6px 0 4px 10px;
    border: 1px solid #cecece;
    background: red;
    border-radius: 8px;
}

.error {
    font-size: 15px;
    font-weight: bold;
    color: red;
}
.spanClassName{
    background-color: #a35989;
}